<template>
   <div>
      <el-card>
          <el-row :gutter="20">
              <el-col :span="16"><div class="grid-content bg-purple"></div></el-col>
          </el-row>
          <div class="space"></div>
          <el-form :inline="true" :model="form" class="demo-form-inline">
              <el-row>
                  <el-form-item label="" size="small">
                      <el-input  v-model="form.hdCode" placeholder=""></el-input>
                  </el-form-item>
                  <el-form-item label="" size="small" >
                      <el-select  v-model="selectAllBrands" placeholder="">
                          <el-option v-for="(item,index) in allBrandsData" :key="item.brandCode" :label="item.name" :value="item.brandCode"></el-option>
                      </el-select>
                  </el-form-item>
                  <el-form-item label="" size="small" >
                      <el-select v-model="selectSpeciSns" placeholder="" :disabled="id?true:false" :clearable="id?true:false"   >
                          <el-option v-for="(item,index) in allSpecisData" :key="item.product.sn" :label="item.product.allName" :value="item.product.sn"></el-option>
                      </el-select>
                  </el-form-item>
              </el-row>
              <el-row>
                  <el-form-item label="" size="small" >
                      <el-select v-model="form.status" placeholder="">
                          <el-option v-for="(name,code) in orderStatusData" :key="code" :label="name" :value="code"></el-option>
                  </el-select>
                  </el-form-item>
                  <el-form-item label="" size="small" >
                      <el-input v-model="form.orderCode" placeholder=""></el-input>
                  </el-form-item>
                  <el-form-item label="" size="small" >
                      <el-select v-model="selectAllarea" placeholder="">
                        <!--  <el-option v-for="item in allareaData" :key="item.code" :label="item.name" :value="item.code"></el-option>-->
                          <el-option-group v-for="group in allareaData":key="group.value":label="group.label">
                              <el-option  v-for="item in group.children"  :key="item.value"  :label="item.label" :value="item.value"></el-option>
                          </el-option-group>
                      </el-select>
                  </el-form-item>
              </el-row>
              <el-row>
                  <el-form-item label=""  size="small">
                     <el-date-picker  v-model="form.stime" type="date" placeholder="" format="yyyy-MM-dd" value-format="yyyy-MM-dd"> </el-date-picker>
                  </el-form-item>
                  <el-form-item label="" size="small" >
                      <el-date-picker  v-model="form.etime" type="date" placeholder="" format="yyyy-MM-dd" value-format="yyyy-MM-dd"> </el-date-picker>
                  </el-form-item>
                  <el-form-item label="" size="small">
                      <el-input  v-model="form.winnerMobile" placeholder=""></el-input>
                  </el-form-item>
              </el-row>
              <el-form-item>
                  <el-row>
                      <el-button  size="small" type="primary" v-on:click="queryData"></el-button>
                      <el-button  size="small" v-on:click="resetData"></el-button>
                      <el-button  size="small" ><el-upload
                              class="upload-demo"
                              action="/api/saotx/order/import"
                              :headers="headers"
                              :data="addPool"
                              :before-upload="handlerSourceFileBUpload"
                              :on-success="handleSourceFileSuccess"
                              :on-remove="handleSourceFileRemove"
                              :file-list="sourceFiles"
                              :auto-upload="true">
                          <el-button slot="trigger" size="small" type="primary"></el-button>
                      </el-upload></el-button>
                      <el-button size="small" plain  v-on:click="exportData" ></el-button>
                  </el-row>
              </el-form-item>
          </el-form>
          <el-table :data="listData" style="width: 100%" border>
              <el-table-column prop="orderCode" label="" ></el-table-column>
              <el-table-column prop="actCode" label="ID" ></el-table-column>
              <el-table-column prop="actName" label=""></el-table-column>
              <el-table-column prop="nickname" label=""></el-table-column>
              <el-table-column prop="winnerMobile" label=""></el-table-column>
              <el-table-column prop="prizeName" label=""></el-table-column>
              <el-table-column prop="snName" label=""></el-table-column>
              <el-table-column  label="">
                  <template slot-scope="scope">
                      {{new Date(scope.row.ctime).Format("yyyy-MM-dd hh:mm:ss")}}
                  </template>
              </el-table-column>
              <el-table-column  label="">
                  <template slot-scope="scope">
                      <span>{{scope.row.awdProvinceName}}-{{scope.row.awdCityName}}-{{scope.row.awdDistrictName}}</span>
                  </template>
              </el-table-column>
              <el-table-column  label="	">
                  <template slot-scope="scope">
                      <span>{{ scope.row.status==1?'':scope.row.status==2?'':'' }}</span>
                  </template>
              </el-table-column>
              <el-table-column
                      fixed="right"
                      label=""
                      width="100">
                  <template slot-scope="scope">
                      <el-button v-on:click="ObjectPageDatail(scope.row.orderCode)" type="text" size="small"></el-button>
                  </template>
              </el-table-column>
          </el-table>
          <!--  -->
          <el-pagination background layout="total, prev, pager, next, jumper"
                         @size-change="sizeChange" @current-change="currentChange"
                         :current-page="form.pageNo" :page-size="form.pageSize"
                         :total="pagination.total">
          </el-pagination>
      </el-card>
   </div>
</template>

<script>
import draggable from 'vuedraggable'

    export default {
        components: {
            draggable
        },
        data() {
            return {
                listData: [],
                orderStatusData:[], //
                allBrandsData: [], // 
                allSpecisData: [], // 
                allareaData:[],// 
                selectAllBrands:'',
                selectSpeciSns:'',
                selectAllarea:'',
                id:'1',
                form: {
                    brandArr:[],
                    snArr:[],
                    cityArr:[],
                    hdCode: '', // 
                    orderCode:'',
                    etime:"",
                    stime:"",
                    status:"",
                    metraType: 1,
                    winnerMobile:"",
                    pageNo: 1,
                    pageSize: 10,
                },
                pagination: { // 
                    total: 0
                },
                //
                currentPage: 1,


                headers: {
                    "token": sessionStorage.getItem("access_token"),
                    "loginId": sessionStorage.getItem("access_loginId")
                },
                addPool: {

                },
                sourceFiles: [] // {name:'', sourceCode: ''}
            }
        },
        name: "Object",
        created() {
            this.init()
        },
        watch:{//
            selectAllBrands(nval, oval){
                if(nval&&nval!=oval) {
                    this.form.brandArr  = [];
                    this.form.brandArr[0] = nval;
                    this.id='';
                    this.selectSpeciSns=''
                    this.form.snArr  = [];
                    this.allSpecisList(this.form.brandArr[0]);
                }
            },
            selectSpeciSns(nval, oval){
                if(nval&&nval!=oval) {
                    this.form.snArr  = [];
                    this.form.snArr[0] = nval;
                }
            },
            selectAllarea(nval, oval){
                if(nval&&nval!=oval) {
                    this.form.cityArr  = [];
                    this.form.cityArr[0] = nval;
                }
            }
        },
        methods: {
            currentChange(pageNo) {
                this.form.pageNo = pageNo;
                // pageNo
                this.getlistData();
            },
            sizeChange(pageSize) {
                // pageSize
                this.form.pageSize = pageSize;
            },
            init(){
                this.orderStatusList()
                this.allBrandsList();
                this.allareaList();
                this.getlistData();
            },
            orderStatusList() {//
                this.$request.post(`/api/saotx/order/statusSelect`,{service: 'browser'},true,res => {
                        console.log(res.data)
                        if (res.ret === '200000') {
                            this.orderStatusData = res.data;
                        }
                    }
                ),
                    err => {
                        console.log(err)
                    }
            },
            allBrandsList(){//
                this.$request.post(`/api/saotx/prod/listBrand`,{service: 'browser'},true,res => {
                        if (res.ret === '200000') {
                            this.allBrandsData = res.data.list;
                        }
                    }
                ),
                    err => {
                        console.log(err)
                    }
            },
            allSpecisList(nval){//
                var brandCodeArr = [];
                brandCodeArr.push(nval);
                this.$request.post(`/api/saotx/prod/listTbc`,{
                    brandCodeArr
                    },true,res => {
                        if (res.ret === '200000') {
                            this.allSpecisData=[];
                            this.allSpecisData = res.data.list;
                        }
                    }
                ),
                    err => {
                        console.log(err)
                    }
            },
            allareaList(){//
                var brandListArrObj = {parentCode: '',withRegion:true}
                let thisnext =this;
                thisnext.$request.post(`/api/saotx/dim/regionByPCode`,{
                        brandListArrObj
                    },true,(res) => {
                        if (res.ret === '200000') {
                            var provinceArr = res.data;
                            if (provinceArr.length != 0) {
                                provinceArr.forEach(function (n ,index) {
                                    var group = {
                                        label: n.name,
                                        value: n.code,
                                        children: []
                                    };
                                    thisnext.$request.post(`/api/saotx/dim/regionByPCode`,{parentCode: n.code},true,(res) => {
                                        var cityArr = res.data;
                                        if(cityArr!=null){
                                            if (cityArr.length != 0) {
                                                cityArr.forEach(function (n, index) {
                                                    group['children'].push({
                                                        label: n.name,
                                                        value: n.code
                                                    })
                                                })
                                            }
                                        }
                                        thisnext.allareaData.push(group);
                                    })
                                })
                            }
                        }
                    }
                ),
                    err => {
                        console.log(err)
                    }
            },
            getlistData(){//
                this.$request.post(`/api/saotx/order/list`,this.form,true,
                    res => {
                        console.log(res.data)
                        if (res.ret === '200000') {
                            this.listData = res.data.list;
                            this.initPagination(res.data.page||{});
                            //this.listData = res.data;
                        }
                    }
                ),
                    err => {
                        console.log(err)
                    }
            },
            // page = {"pageCount":, "count":}
            initPagination(page) {
                if(page) {
                    this.pagination.total = page.count;
                }
            },
            queryData: function(event){//
                this.getlistData();
            },
            resetData: function(event){//
                this.selectAllBrands='';
                 this.selectSpeciSns='';
                this.selectAllarea='';
                this.id='1';
                this.form = {
                    metraFlag: 'Object',
                    hdCode: '', // 
                    orderCode:'',//
                    brandArr:[],//
                    snArr:[],//
                    cityArr:[],//
                    metraType: 1, // 
                    status: '', // 
                    stime: '', // 
                    etime: '', // 
                    winnerMobile:'',
                    pageNo: 1,
                    pageSize: 10
                }
                this.getlistData();
            },
            // 
            handlerSourceFileBUpload(file) {
            },
            // 
            handleSourceFileSuccess(res, file) {
                if(res.ret==200000) {
                    this.$message({type:'success', message:res.data.successMsg});
                    let obj = {name:res.data.sourceFile, sourceCode:res.data.sourceCode, count:res.data.successCount};
                    this.sourceFiles.push(obj);
                } else {
                    this.$message.error(res.message);
                }
            },
            // 
            handleSourceFileRemove(file, fileList) {
                this.sourceFiles = []; // 
            },
            exportData(){//
                var url = "/api/saotx/order/export";
                var xhr = new XMLHttpRequest();
                var formData = new FormData();
                for(var attr in this.form) {
                    formData.append(attr, this.form[attr]);
                }
                xhr.overrideMimeType("text/plain; charset=x-user-defined");
                xhr.open('POST', url, true);
                xhr.responseType = "blob";
                xhr.responseType = "arraybuffer"
                xhr.setRequestHeader("token", sessionStorage.getItem('access_token'));
                xhr.setRequestHeader("loginId", sessionStorage.getItem('access_loginId'));
                xhr.onload = function(res) {
                    if (this.status == 200) {
                        var blob = new Blob([this.response], {type: 'application/vnd.ms-excel'});
                        var respHeader = xhr.getResponseHeader("Content-Disposition");
                        var fileName = decodeURI(respHeader.match(/filename=(.*?)(;|$)/)[1]);
                        if (window.navigator.msSaveOrOpenBlob) {
                            navigator.msSaveBlob(blob, fileName);
                        } else {
                            var link = document.createElement('a');
                            link.href = window.URL.createObjectURL(blob);
                            link.download = fileName;
                            link.click();
                            window.URL.revokeObjectURL(link.href);
                        }
                    }
                }
                xhr.send(formData);
            },
            ObjectPageDatail(orderId){//
                this.$router.push({path:'/Orders/ACT/objectDetail?orderId='+orderId})
            }
        }


    }
</script>

<style lang="scss"  scoped>
    .space {position:relative;width:100%;height:20px;}
</style>



