<template>
  <div class="actMgr-container">
    <el-card>
      <el-button type="primary" size="small" class="ml20" @click="addAct"></el-button>
      <el-form label-width="80px" label-position="left">
        <el-row :gutter="30">
          <el-col :span="8">
            <el-form-item label=":">
              <el-select size="small" v-model="queryActParams.brandCodeArr" multiple placeholder="" @change="getSonBrandList">
                <el-option v-for="item in brandList" :key="item.brandCode" :label="item.name" :value="item.brandCode">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label=":">
              <el-select size="small" v-model="queryActParams.snArr" multiple placeholder="">
                <el-option v-for="item in sonBrandList" :key="item.sn" :label="item.allName" :value="item.sn">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label=":">
              <el-select size="small" v-model="queryActParams.status" placeholder="">
                <el-option v-for="(value, key) in actStatusObj" :key="key" :label="value" :value="key">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="30">
          <el-col :span="6">
            <el-form-item label=":">
              <el-input size="small" v-model="queryActParams.keywords" placeholder=""></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="16" :offset="2">
            <el-form-item label=":">
              <el-select size="small" :disabled="isDisabled" v-model="selectProvList" multiple collapse-tags placeholder="" @change="getCityList">
                <el-option v-for="item in provList" :key="item.code" :label="item.name" :value="item.code">
                </el-option>
              </el-select>
              <el-select size="small" :disabled="isDisabled" v-model="selectCityList" multiple collapse-tags placeholder="" @change="selectArea">
                <el-option v-for="item in cityList" :key="item.code" :label="item.name" :value="item.code">
                </el-option>
              </el-select>
              <el-checkbox size="small" v-model="isDisabled" label="" border></el-checkbox>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="30">
          <el-col :span="10">
            <el-form-item label=":">
              <el-date-picker size="small" v-model="actTime" format="yyyy-MM-dd" value-format="yyyy-MM-dd" type="datetimerange" :editable="false" range-separator="" start-placeholder="" end-placeholder="">
              </el-date-picker>
            </el-form-item>
          </el-col>
          <el-col>
            <el-button type="primary" size="small" @click="queryActList"></el-button>
            <el-button type="primary" size="small" @click="resetForm"></el-button>
          </el-col>
        </el-row>
      </el-form>
    </el-card>
    <el-card class="mt20">
      <el-table v-loading="loading" :data="actList" border style="width: 100%" :stripe="true">
        <el-table-column type="index" width="50" label="" align="center">
        </el-table-column>
        <el-table-column property="hdCode" label="" align="center" width="180px">
        </el-table-column>
        <el-table-column property="actName" label="" align="center">
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            {{scope.row.stimeStr}}{{scope.row.etimeStr}}
          </template>
        </el-table-column>
        <el-table-column property="statusName" label="" align="center" width="100px">
        </el-table-column>
        <el-table-column label="" align="center" width="260px">
          <template slot-scope="scope">
            <a style="color: #347ab7" href="javascript:;" v-if="scope.row.status != 4" @click="edit(scope.row.id,scope.row.form)"></a>
            <a style="color: #347ab7" href="javascript:;" v-if="scope.row.status == 2" @click="post(scope.row.id)"></a>
            <a style="color: #347ab7" href="javascript:;" v-if="scope.row.status == 3" @click="post(scope.row.id)"></a>
            <a style="color: #347ab7" href="javascript:;" @click="clone(scope.row.id,scope.row.form)"></a>
            <a style="color: #347ab7" href="javascript:;" @click="getLogList(scope.row.actCode)"></a>
            <a style="color: #347ab7" href="javascript:;" v-if="scope.row.status == 1" @click="stop(scope.row.id)"></a>
            <a style="color: #347ab7" href="javascript:;" v-if="scope.row.status == 1" @click="over(scope.row.id)"></a>
            <a style="color: #347ab7" href="javascript:;" v-if="scope.row.status == 4" @click="del(scope.row.id)"></a>
            <a style="color: #347ab7" href="javascript:;" v-if="scope.row.status != 4 && scope.row.status != 0" @click="editTpl(scope.row.form, scope.row.actCode)"></a>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination v-if="actForms" background @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryActParams.pageNo" :page-size="queryActParams.pageSize" layout="total, prev, pager, next, jumper" :total="actListTotal">
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog :visible.sync="addActDialogVisible" width="900px" :close-on-click-modal="false">
      <div class="act-wrap">
        <div class="title">
          <ul>
            <li v-for="(item, index) in actFormName" :key="index" @click="getCheckedAct(item, index)" :class="index == nowActiveIndex ? 'active' : ''">{{item.name}}</li>
          </ul>
          <div style="clear: both"></div>
        </div>
        <div v-if="actForms.length !== 0">
          <div class="act-item" v-for="item in actForms" :key="item.id">
            <img :src="item.extUrl" :alt="item.name">
            <p class="act-name">{{item.name}}</p>
            <p class="desc">{{item.note ? item.note : '~'}}</p>
            <div class="btn">
              <el-button type="primary" size="small"></el-button>
              <el-button type="primary" size="small" @click="goPut(item)"></el-button>
            </div>
          </div>
        </div>
        <div v-else></div>
      </div>
      <el-col :span="24" v-show="actForms">
        <el-pagination background @size-change="actHandleSizeChange" @current-change="actHandleCurrentChange" :current-page="actParams.pageNo" :page-size="actParams.pageSize" layout="total, prev, pager, next, jumper" :total="actTotal">
        </el-pagination>
      </el-col>
      <div style="clear: both"></div>
    </el-dialog>
    <!--  -->
    <el-dialog :visible.sync="putLogVisible" width="900px" :close-on-click-modal="false">
      <el-row>
        <el-col :span="8">
          
          <el-select v-model="putLogTime" placeholder="" @change="logDetail(nowActCode, putLogTime)">
            <el-option v-for="(item, index) in putTimeList" :key="index" :label="new Date(item.time).Format('yyyy-MM-dd hh:mm:ss')" :value="item.version">
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="12" :offset="3">
          
        </el-col>
      </el-row>
      <div>
        <div class="mt20"></div>
        <el-table :data="normalLogList" border style="width: 100%">
          <el-table-column type="index" width="50" label="">
          </el-table-column>
          <el-table-column prop="awardName" label="">
          </el-table-column>
          <el-table-column prop="totalNum" label="">
          </el-table-column>
          <el-table-column prop="outNum" label="">
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              {{scope.row.totalNum - scope.row.outNum}}
            </template>
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              {{scope.row.probability + '%'}}
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div v-if="firstScanLogList.length != 0">
        <div class="mt20"></div>
        <el-table :data="normalLogList" border style="width: 100%">
          <el-table-column type="index" width="50" label="">
          </el-table-column>
          <el-table-column prop="awardName" label="">
          </el-table-column>
          <el-table-column prop="totalNum" label="">
          </el-table-column>
          <el-table-column prop="outNum" label="">
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              {{scope.row.totalNum - scope.row.outNum}}
            </template>
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              {{scope.row.probability + '%'}}
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div v-if="nWinLogList.length != 0">
        <div class="mt20">n</div>
        <el-table :data="normalLogList" border style="width: 100%">
          <el-table-column type="index" width="50" label="">
          </el-table-column>
          <el-table-column prop="awardName" label="">
          </el-table-column>
          <el-table-column prop="totalNum" label="">
          </el-table-column>
          <el-table-column prop="outNum" label="">
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              {{scope.row.totalNum - scope.row.outNum}}
            </template>
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              {{scope.row.probability + '%'}}
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div v-if="fixationLogList.length != 0">
        <div class="mt20"></div>
        <el-table :data="normalLogList" border style="width: 100%">
          <el-table-column type="index" width="50" label="">
          </el-table-column>
          <el-table-column prop="awardName" label="">
          </el-table-column>
          <el-table-column prop="totalNum" label="">
          </el-table-column>
          <el-table-column prop="outNum" label="">
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              {{scope.row.totalNum - scope.row.outNum}}
            </template>
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              {{scope.row.probability + '%'}}
            </template>
          </el-table-column>
        </el-table>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      queryActParams: {
        allFormCode: '',
        brandCodeArr: [],
        cityCodeArr: [],
        etime: '',
        form: '',
        hdCode: '',
        keywords: '',
        pageNo: 1,
        pageSize: 10,
        provinceCodeArr: [],
        snArr: [],
        status: '',
        stime: ''
      },
      actListTotal: 0,
      addActDialogVisible: false,
      actFormName: [
        { name: '', code: '' }
        // { name: '' },
        // { name: '' },
        // { name: '' },
        // { name: '' },
        // { name: '' },
        // { name: '' }
      ],
      actParams: {
        pageNo: 1,
        pageSize: 4,
        pcode: ''
      },
      actForms: [],
      actTotal: 0,
      nowActiveIndex: 0,
      actList: [], // 
      actStatusObj: {}, // 
      brandList: [], // 
      sonBrandList: [], // 
      provList: [], // 
      selectProvList: [],
      cityList: [], // 
      selectCityList: [],
      isDisabled: false,
      value: '',
      actTime: '',
      nowActCode: '', // code
      putLogVisible: false,
      putTimeList: [],
      putLogTime: '',
      normalLogList: [],
      firstScanLogList: [],
      nWinLogList: [],
      fixationLogList: [],
      loading: true,
      formPath: {
        'act-101': '/market/actTpl/AddActSudoku?edit=',
        'act-102': '/market/actTpl/AddWingAct?edit=',
        'act-103': '/market/actTpl/addActEgg?edit=',
        'act-104': '/market/actTpl/addActFanpaizi?edit=',
        'act-501': '/market/actTpl/addActQuestion?edit=',
        'act-301': '/market/actTpl/addActRedPacked?edit=',
        'act-105': '/market/actTpl/addRound?edit=',
        'act-100': '/market/actTpl/addAct?edit='
      }
    }
  },
  created() {
    this.init()
  },
  watch: {
    isDisabled: function(newQuestion, oldQuestion) {
      if (newQuestion) {
        this.selectProvList = []
        this.selectCityList = []
        this.queryActParams.provinceCodeArr = ['000000']
        this.queryActParams.cityCodeArr = ['000000']
      } else {
        this.queryActParams.provinceCodeArr = []
        this.queryActParams.cityCodeArr = []
      }
    },
    actTime: function(val) {
      if (val && val.length === 2) {
        this.queryActParams.stime = this.actTime[0]
        this.queryActParams.etime = this.actTime[1]
      } else {
        this.queryActParams.stime = ''
        this.queryActParams.etime = ''
      }
    }
  },
  methods: {
    // 
    init() {
      this.getActList()
      this.getProvList()
      this.getActStatus()
      this.getBrandList()
      this.getActCodeList()
    },
    // 
    getActList() {
      // if (this.queryActParams.status == '0') this.queryActParams.status = ''
      this.$request.post('/api/saotx/act/list', this.queryActParams, true, res => {
        if (res.ret === '200000') {
          this.actList = res.data.list
          this.actListTotal = res.data.page.count
          this.loading = false
          return
        }
        this.$message.error(res.message)
      })
    },
    // 
    getActStatus() {
      this.$request.post('/api/saotx/act/statusSelect', {}, true, res => {
        if (res.ret === '200000') {
          this.actStatusObj = res.data
          return
        }
        this.$message.error(res.message)
      })
    },
    selectArea() {
      this.queryActParams.provinceCodeArr = this.selectProvList
      this.queryActParams.cityCodeArr = this.selectCityList
    },
    // 
    getBrandList() {
      this.$request.post('/api/saotx/prod/listBrand', { pageSize: '-1' }, true, res => {
        if (res.ret === '200000') return (this.brandList = res.data.list)
      })
    },
    getSonBrandList() {
      if (this.queryActParams.brandCodeArr.length === 0) {
        this.sonBrandList = []
        this.queryActParams.snArr = []
        return
      }
      this.$request.post(
        '/api/saotx/prod/list',
        { brandCodeArr: this.queryActParams.brandCodeArr, pageSize: '-1' },
        true,
        res => {
          if (res.ret === '200000') return (this.sonBrandList = res.data.list)
          this.$message.error(res.message)
        }
      )
    },
    // 
    getProvList() {
      this.$request.post('/api/saotx/dim/regionByMultiParent', { parentArr: [] }, true, res => {
        if (res.ret === '200000') {
          this.provList = res.data
          return
        }
        this.$message.error(res.message)
      })
    },
    // 
    getCityList() {
      if (this.selectProvList.length == 0) {
        this.cityList = []
        this.selectCityList = []
        return
      }
      this.$request.post(
        '/api/saotx/dim/regionByMultiParent',
        { parentArr: this.selectProvList },
        true,
        res => {
          if (res.ret === '200000') {
            this.cityList = res.data
            return
          }
          this.$message.error(res.message)
        }
      )
    },
    // 
    addAct() {
      this.addActDialogVisible = true
      this.actParams.pcode = ''
      this.actParams.pageNo = 1
      this.nowActiveIndex = 0
      this.getAct()
    },
    // tab
    getCheckedAct(item, index) {
      this.nowActiveIndex = index
      this.actParams.pcode = item.code
      this.actParams.pageNo = 1
      // if (index == 0) {
      //   this.actParams.pcode = ''
      // } else {
      //   this.actParams.pcode = 'form-cate' + index
      // }
      this.getAct()
    },
    // 
    getActCodeList() {
      this.$request.post(
        '/api/saotx/act/formByPCode',
        {
          pCode: '',
          pageNo: 1,
          pageSize: -1
        },
        true,
        res => {
          if (res.ret == '200000') {
            this.actFormName.push(...res.data.list)
            // this.getAct()
          } else {
            this.$message.error(res.message)
          }
        }
      )
    },
    // 
    getAct() {
      this.$request.post(
        '/api/saotx/acttpl/list',
        this.actParams,
        true,
        res => {
          if (res.ret == '200000') {
            this.actForms = res.data.list
            this.actTotal = res.data.page.count
          } else {
            this.$message.error(res.message)
          }
        },
        err => {
          console.log(err)
        }
      )
    },
    // 
    queryActList() {
      this.queryActParams.pageNo = 1
      this.getActList()
    },
    // 
    resetForm() {
      this.queryActParams.keywords = ''
      this.queryActParams.brandCodeArr = []
      this.queryActParams.snArr = []
      this.queryActParams.status = ''
      this.queryActParams.stime = ''
      this.queryActParams.etime = ''
      this.selectProvList = []
      this.selectCityList = []
      this.queryActParams.provinceCodeArr = []
      this.queryActParams.cityCodeArr = []
      this.actTime = []
      this.queryActParams.pageNo = 1
      this.getActList()
    },
    // 
    getLogList(code) {
      this.$request.post('/api/saotx/act/hisVers', { actCode: code }, true, res => {
        if (res.ret == '200000') {
          this.putLogVisible = true
          this.normalLogList = []
          this.firstScanLogList = []
          this.nWinLogList = []
          this.fixationLogList = []
          this.putTimeList = []
          this.putLogTime = ''
          if (res.data.length > 0) {
            this.nowActCode = code
            this.putTimeList = res.data
            this.putLogTime = res.data[0].version
            this.logDetail(code, this.putLogTime)
          }
          return
        }
        this.$message.error(res.message)
      })
    },
    // 
    logDetail(code, time) {
      this.$request.post(
        '/api/saotx/act/hisDetail',
        {
          actCode: code,
          version: time
        },
        true,
        res => {
          if (res.ret === '200000') {
            res.data.strategyArr.forEach(item => {
              if (item.tfType == 'common') this.normalLogList = item.awardArr
              if (item.tfType == 'sn_first') this.firstScanLogList = item.awardArr
              if (item.tfType == 'n_mwin') this.nWinLogList = item.awardArr
              if (item.tfType == 'special') this.fixationLogList = item.awardArr
            })
            return
          }
          this.$message.error(res.message)
        }
      )
    },
    // 
    handleSizeChange(newSize) {
      this.queryActParams.pageSize = newSize
      this.getActList()
    },
    // 
    handleCurrentChange(newPage) {
      this.queryActParams.pageNo = newPage
      this.getActList()
    },
    // 
    actHandleSizeChange(newSize) {
      this.actParams.pageSize = newSize
      this.getAct()
    },
    // 
    actHandleCurrentChange(newPage) {
      this.actParams.pageNo = newPage
      this.getAct()
    },
    // 
    goPut(item) {
      this.$router.push(
        '/market/actTpl/actSetConf?form=' +
          item.form +
          '&tplCode=' +
          item.tplCode
      )
    },

    // 
    edit(id,form) {
    	// if(form=='act-501'){
    		this.$router.push('/market/actTpl/actSetConf?id=' + id+'&form='+form)
    	// }else {
    		// this.$router.push('/market/actTpl/actSetConf?id=' + id)
    	// }
    },
    // 
    clone(id,form) {
      this.$router.push('/market/actTpl/actSetConf?id=' + id + '&clone=1&form='+form)
    },
    // 
    async post(id) {
      const confirmResult = await this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message({
          type: 'info',
          message: ''
        })
      }
      this.$request.post('/api/saotx/act/modifyStatus', { id: id, status: 1 }, true, res => {
        if (res.ret == '200000') {
          this.$message.success('')
          this.getActList()
          return
        }
        this.$message.error(res.message)
      })
    },
    // 
    async over(id) {
      const confirmResult = await this.$confirm(
        '',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message({
          type: 'info',
          message: ''
        })
      }
      this.$request.post('/api/saotx/act/modifyStatus', { id: id, status: 4 }, true, res => {
        if (res.ret == '200000') {
          this.$message.success('')
          this.getActList()
          return
        }
        this.$message.error(res.message)
      })
    },
    // 
    async stop(id) {
      const confirmResult = await this.$confirm(
        '',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message({
          type: 'info',
          message: ''
        })
      }
      this.$request.post('/api/saotx/act/modifyStatus', { id: id, status: 3 }, true, res => {
        if (res.ret == '200000') {
          this.$message.success('')
          this.getActList()
          return
        }
        this.$message.error(res.message)
      })
    },
    // 
    async del(id) {
      const confirmResult = await this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message({
          type: 'info',
          message: ''
        })
      }
      this.$request.post('/api/saotx/act/remBatch', { idArr: [id] }, true, res => {
        if (res.ret == '200000') {
          this.$message.success('')
          this.getActList()
          return
        }
        this.$message.error(res.message)
      })
    },
    editTpl(form, code) {
      this.$router.push(this.formPath[form] + code)
      //  switch (form) {
      //   case 'act-101':
      //     this.$router.push('/market/actTpl/AddActSudoku?edit=' + code)
      //     break; 
      //   case 'act-102':
      //     this.$router.push('/market/actTpl/AddWingAct?edit=' + code)
      //     break;
      //   case 'act-103':
      //     this.$router.push('/market/actTpl/addActEgg?edit=' + code)
      //     break;
      //   case 'act-104':
      //     this.$router.push('/market/actTpl/addActFanpaizi?edit=' + code)
      //     break;
      //   case 'act-501':
      //     this.$router.push('/market/actTpl/addActQuestion?edit=' + code)
      //     break;
      //   case 'act-301':
      //     this.$router.push('/market/actTpl/addActRedPacked?edit=' + code)
      //     break;
      //   case 'act-100':
      //     this.$router.push('/market/actTpl/addAct?edit=' + code)
      //     break;
      //   case 'act-105':
      //     this.$router.push('/market/actTpl/addRound?edit=' + code)
      //     break;
      // }
      // this.$request.post('/api/saotx/act/pubTpl', {actCode: code}, true, res => {
      //   if (res.ret === '200000') {
      //     console.dir(JSON.parse(res.data.conf).img)
      //   }
      // })
    }
  }
}
</script>
<style lang="scss" scoped>
.el-form {
  margin: 20px;
}
.el-col {
  vertical-align: middle;
}

// 
.title {
  border-bottom: 1px solid #ccc;
  margin-bottom: 15px;
  li {
    float: left;
    font-size: 18px;
    cursor: pointer;
    padding: 10px;
  }
  li.active {
    color: #409eff;
  }
  
}
.act-name {
  margin: 10px 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.el-dialog__body {
  padding-top: 10px !important;
}
.act-item {
  float: left;
  width: 200px;
  text-align: center;
  border: 1px solid #e5e5e5;
  border-radius: 5px;
  padding: 8px 12px 10px 11px;
  margin: 0px 15px 0px 0px;
  box-sizing: border-box;
  img {
    width: 100%;
  }
  p {
    i {
      float: right;
      font-size: 20px;
      color: #409eff;
      cursor: pointer;
      margin-top: 5px;
    }
  }
}
.el-pagination {
  margin-top: 20px;
}
.desc {
  color: #a0a0a0;
  margin: 0;
  margin-bottom: 10px;
  // margin: 14px 0 0 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
</style>



