<template>
  <!--
  Author: chenxin
  Create Date: 2018-10-18
  Description: 
  -->
  <div class="addActTamConf-container">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-tabs v-model="activeName">
        <el-tab-pane label="" name="home" class="clearfix">
          <div class="ipone">
            <div class="header">
              <p>{{addActParams.name}}</p>
            </div>
            <div class="content">
              <div class="bg"><img :src="configItem.bgImgUrl" alt="" title="" @click="showEditConIndex = 1"></div>
              <div class="top"><img :src="configItem.headerImgUrl" alt="" title="" @click="showEditConIndex = 2"></div>
              <!-- <div class="kits"><img :src="defaultConf.img.tips.url" alt="" title="" @click="showEditConIndex = 3" ></div> -->
              <div class="game-con">
                <div class="game-item" v-for="item in configItem.iconUrl" :key="item.key" @click="showEditConIndex = 3"><img :src="item.imgUrl" alt=""></div>
              </div>
            </div>
            <div class="footer"></div>
          </div>
          <div class="edit-box">
            <el-form :model="addActParams" :rules="addActRules" ref="ruleForm" label-width="100px" label-position="top">
              <el-form-item label="" prop="name">
                <el-input v-model="addActParams.name" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input type="textarea" :rows="3" v-model="addActParams.note" placeholder="" maxlength="50" resize="none"></el-input>
              </el-form-item>
            </el-form>
            <el-card class="edit-img" shadow="hover">
              <div slot="header" class="clearfix">
                <span></span>
              </div>
              <div class="edit-con">
                <div class="edit-bg-img" v-if="showEditConIndex == 1">
                  <p class="img-title">:</p>
                  <div class="img-con"><img :src="configItem.bgImgUrl" alt=""></div>
                  <div class="btn-con">
                    <el-upload :action="uploadURL" :before-upload="beforeAvatarUpload" :headers="headerObj" :on-success="upBgImgSuccess" :show-file-list="false">
                      <el-button size="small" type="primary"></el-button>
                    </el-upload>
                  </div>
                </div>
                <div class="edit-top-img" v-if="showEditConIndex == 2">
                  <p class="img-title">:</p>
                  <div class="img-con"><img :src="configItem.headerImgUrl" alt=""></div>
                  <div class="btn-con">
                    <el-upload :action="uploadURL" :before-upload="beforeAvatarUpload" :headers="headerObj" :on-success="upTopImgSuccess" :show-file-list="false">
                      <el-button size="small" type="primary"></el-button>
                    </el-upload>
                  </div>
                </div>
                <div class="edit-game-img" v-if="showEditConIndex == 3">
                  <div v-for="(item, index) in configItem.iconUrl" :key="item.key">
                    <p class="img-title">{{'' + (index + 1) + ':'}}</p>
                    <div class="img-con"><img :src="item.imgUrl" alt=""></div>
                    <div class="btn-con">
                      <el-upload :action="uploadURL" :before-upload="beforeAvatarUpload" :headers="headerObj" :on-success="upGameImgSuccess" :show-file-list="false">
                        <el-button size="small" type="primary" @click="getGameIndex(index)"></el-button>
                      </el-upload>
                    </div>
                  </div>
                </div>
                <p class="tips" v-if="showEditConIndex == 1">*  750*1208px*.jpg\ *.bmp\ *.png\ *.gif</p>
                <p class="tips" v-if="showEditConIndex == 2">*  700*350px*.jpg\ *.bmp\ *.png\ *.gif</p>
                <p class="tips" v-if="showEditConIndex == 3">*  186*162px*.jpg\ *.bmp\ *.png\ *.gif</p>
              </div>

            </el-card>
          </div>
        </el-tab-pane>
        <el-tab-pane label="" name="actDesc" class="clearfix">
          <div class="ipone">
            <div class="header"></div>
            <div class="content">
              <div class="bg"><img src="https://weiopn.oss-cn-beijing.aliyuncs.com/pc_data_front/img/huodongshuoming.png"></div>
            </div>
            <div class="footer"></div>
          </div>
        </el-tab-pane>
        <el-tab-pane label="" name="myPrize" class="clearfix">
          <div class="ipone">
            <div class="header"></div>
            <div class="content">
              <div class="bg"><img src="https://weiopn.oss-cn-beijing.aliyuncs.com/pc_data_front/img/wodedjiangpin.png"></div>
            </div>
            <div class="footer"></div>
          </div>
        </el-tab-pane>
        <el-tab-pane label="" name="winning" class="clearfix">
          <div class="ipone">
            <div class="header"></div>
            <div class="content">
              <div class="bg"><img :src="configItem.bgImgUrl"></div>
              <div class="winning">
                <div class="prize-con">
                  <div class="close">X</div>
                  <img class="pic" :src="configItem.drawImgUrl" alt="">
                  <h3></h3>
                  <p>24</p>
                  <img class="prize-btn" :src="configItem.getBtn" alt="">
                </div>
              </div>
            </div>
            <div class="footer"></div>
          </div>
          <div class="edit-box">
            <el-card class="edit-img" shadow="hover">
              <div slot="header" class="clearfix">
                <span></span>
              </div>
              <div class="edit-con">
                <div class="edit-winning-img">
                  <p class="img-title">:</p>
                  <div class="img-con"><img :src="configItem.getBtn" alt=""></div>
                  <div class="btn-con">
                    <el-upload :action="uploadURL" :before-upload="beforeAvatarUpload" :headers="headerObj" :on-success="upGetBtnImgSuccess" :show-file-list="false">
                      <el-button type="primary" size="small"></el-button>
                    </el-upload>
                  </div>
                </div>
                <p class="tips">*  344*84px*.jpg\ *.bmp\ *.png\ *.gif</p>
              </div>
            </el-card>
          </div>
        </el-tab-pane>
        <el-tab-pane label="" name="notWinning" class="clearfix">
          <div class="ipone">
            <div class="header"></div>
            <div class="content">
              <div class="bg"><img :src="configItem.bgImgUrl"></div>
              <div class="not-winning">
                <div class="prize-con">
                  <div class="close">X</div>
                  <img class="pic" :src="configItem.cryImgUrl" alt="">
                  <h3></h3>
                  <img class="prize-btn" :src="configItem.knowBtn" alt="">
                </div>
              </div>
            </div>
            <div class="footer"></div>
          </div>
          <div class="edit-box">
            <el-card class="edit-img" shadow="hover">
              <div slot="header" class="clearfix">
                <span></span>
              </div>
              <div class="edit-con">
                <div class="edit-notWinning-img">
                  <p class="img-title">:</p>
                  <div class="img-con"><img :src="configItem.cryImgUrl" alt=""></div>
                  <div class="btn-con">
                    <el-upload :action="uploadURL" :before-upload="beforeAvatarUpload" :headers="headerObj" :on-success="upNotWinningImgSuccess" :show-file-list="false">
                      <el-button size="small" type="primary"></el-button>
                    </el-upload>
                  </div>
                </div>
                <p class="tips">*  620*344px*.jpg\ *.bmp\ *.png\ *.gif</p>
              </div>
              <div class="edit-con">
                <div class="edit-notWinning-img">
                  <p class="img-title">:</p>
                  <div class="img-con"><img :src="configItem.knowBtn" alt=""></div>
                  <div class="btn-con">
                    <el-upload :action="uploadURL" :before-upload="beforeAvatarUpload" :headers="headerObj" :on-success="upKnowBtnImgSuccess" :show-file-list="false">
                      <el-button type="primary" size="small"></el-button>
                    </el-upload>
                  </div>
                </div>
                <p class="tips">*  290*74px*.jpg\ *.bmp\ *.png\ *.gif</p>
              </div>
            </el-card>
          </div>
        </el-tab-pane>
        <div class="btn">
          <p>: <el-switch v-model="isPut">
            </el-switch>
          </p>
          <el-button size="small" type="primary" @click="saveActTpl"></el-button>
        </div>
      </el-tabs>
    </el-card>
  </div>
</template>
<script>
export default {
  props: ['id', 'edit'],
  data() {
    return {
      activeName: 'home',
      addActParams: {
        conf: '',
        form: 'act-100',
        id: '',
        name: '',
        note: ''
      },
      addActRules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { max: 15, message: ' 15 ', trigger: 'blur' }
        ]
      },
      configItem: {
        title: '',
        description: '',
        getBtn: 'https://weiopn.oss-cn-beijing.aliyuncs.com/pc_data_front/img/djlq_btn.png',
        knowBtn: 'https://weiopn.oss-cn-beijing.aliyuncs.com/new_platform/know-btn.png',
        headerImgUrl:
          'http://weiopn.oss-cn-beijing.aliyuncs.com/pc_data_front/img/zuanshi-header.png',
        bgImgUrl: 'http://weiopn.oss-cn-beijing.aliyuncs.com/pc_data_front/img/zuanshi-bg.png',
        iconUrl: [
          {
            key: 1,
            imgUrl: 'http://weiopn.oss-cn-beijing.aliyuncs.com/pc_data_front/img/zuanshi.png',
            $$hashKey: 'object:378'
          },
          {
            key: 2,
            imgUrl: 'http://weiopn.oss-cn-beijing.aliyuncs.com/pc_data_front/img/zuanshi.png',
            $$hashKey: 'object:379'
          },
          {
            key: 3,
            imgUrl: 'http://weiopn.oss-cn-beijing.aliyuncs.com/pc_data_front/img/zuanshi.png',
            $$hashKey: 'object:380'
          },
          {
            key: 4,
            imgUrl: 'http://weiopn.oss-cn-beijing.aliyuncs.com/pc_data_front/img/zuanshi.png',
            $$hashKey: 'object:381'
          },
          {
            key: 5,
            imgUrl: 'http://weiopn.oss-cn-beijing.aliyuncs.com/pc_data_front/img/zuanshi.png',
            $$hashKey: 'object:382'
          },
          {
            key: 6,
            imgUrl: 'http://weiopn.oss-cn-beijing.aliyuncs.com/pc_data_front/img/zuanshi.png',
            $$hashKey: 'object:383'
          },
          {
            key: 7,
            imgUrl: 'http://weiopn.oss-cn-beijing.aliyuncs.com/pc_data_front/img/zuanshi.png',
            $$hashKey: 'object:384'
          },
          {
            key: 8,
            imgUrl: 'http://weiopn.oss-cn-beijing.aliyuncs.com/pc_data_front/img/zuanshi.png',
            $$hashKey: 'object:385'
          }
        ],
        iconAwardImg: 'http://weiopn.oss-cn-beijing.aliyuncs.com/pc_data_front/img/zuanshi.png',
        awardUrl: [
          {
            imgUrl:
              'http://weiopn.oss-cn-beijing.aliyuncs.com/pc_data_front/img/zhongjiang74@2x.png'
          }
        ],
        drawImgUrl:
          'http://weiopn.oss-cn-beijing.aliyuncs.com/pc_data_front/img/zhongjiang74@2x.png',
        cryImgUrl: 'http://weiopn.oss-cn-beijing.aliyuncs.com/pc_data_front/img/cry75@2x.png'
      },
      uploadURL: '/api/saotx/attach/commonAliUpload',
      headerObj: {
        loginId: sessionStorage.getItem('access_loginId') || '2d07e7953a2a63ceda6df5144d1abec3',
        token: sessionStorage.getItem('access_token'),
        CLIENTSESSIONID: sessionStorage.getItem('CLIENTSESSIONID')
      },
      gameIndex: null, // 
      showEditConIndex: 1, // 
      isPut: true //  
    }
  },
  computed: {},
  created() {
    this.getActDetail()
  },
  methods: {
    getActDetail() {
      if (this.id) {
        this.$request.post('/api/saotx/acttpl/detail', { id: this.id }, true, res => {
          if (res.ret === '200000') {
            this.addActParams = res.data
            this.configItem = JSON.parse(res.data.conf)
            console.log(this.configItem)
            if (res.data.statusName == '') {
              this.isPut = false
            } else {
              this.isPut = true
            }
          } else {
            this.$message.error(res.message)
          }
        })
      } else if (this.edit) {
          this.$request.post('/api/saotx/act/pubTpl', {actCode: this.edit}, true, res => {
              if (res.ret === '200000') {
              this.addActParams = res.data
              this.addActParams.name = JSON.parse(res.data.conf).title
              this.addActParams.note = JSON.parse(res.data.conf).description
              this.configItem = JSON.parse(res.data.conf)
              if (res.data.statusName == '') {
                  this.isPut = false
              } else {
                  this.isPut = true
              }
              } else {
              this.$message.error(res.message)
              }
          })
      }
    },
    // 
    upBgImgSuccess(resule) {
      if (resule.ret === '200000') return (this.configItem.bgImgUrl = resule.data.accessUrl)
      this.$message.error(resule.message)
    },
    // 
    upTipsImgSuccess(resule) {
        if (resule.ret === '200000') return (this.defaultConf.img.tips.url = resule.data.accessUrl)
        this.$message.error(resule.message)
    },
    // 
    upTopImgSuccess(resule) {
      if (resule.ret === '200000') return (this.configItem.headerImgUrl = resule.data.accessUrl)
      this.$message.error(resule.message)
    },
    // 
    getGameIndex(index) {
      this.gameIndex = index
    },
    upKnowBtnImgSuccess(resule) {
      if (resule.ret === '200000')
        return (this.configItem.knowBtn = resule.data.accessUrl)
      this.$message.error(resule.message)
    },
    // 
    upGetBtnImgSuccess(resule) {
      if (resule.ret === '200000')
        return (this.configItem.getBtn = resule.data.accessUrl)
      this.$message.error(resule.message)
    },
    // 
    upGameImgSuccess(resule) {
      if (resule.ret === '200000')
        return (this.configItem.iconUrl[this.gameIndex].imgUrl = resule.data.accessUrl)
      this.$message.error(resule.message)
    },
    // 
    upNotWinningImgSuccess(resule) {
      if (resule.ret === '200000') return (this.configItem.cryImgUrl = resule.data.accessUrl)
      this.$message.error(resule.message)
    },
    beforeAvatarUpload(file) {
      const IMGTYPE = file.type === 'image/jpeg' || file.type === 'image/png' || file.type === 'image/bmp' || file.type === 'image/gif'
      if (!IMGTYPE) {
        this.$message.error(' JPG  PNG  GIF  BMP !')
      }
      return IMGTYPE
    },
    // 
    saveActTpl() {
      if (!this.addActParams.name) return this.$message.warning('')
      this.configItem.title = this.addActParams.name
      this.configItem.description = this.addActParams.note
      this.addActParams.conf = JSON.stringify(this.configItem)
      if (this.edit) {
        this.$request.post('/api/saotx/act/mpubTpl', this.addActParams, true, res => {
            if (res.ret === '200000') {
              this.$message.success('')
              this.$router.push('/market/actMgr')
            } else {
              this.$message.error(res.message)
            }
        })
        return
      }
      this.$request.post('/api/saotx/acttpl/saveOrModify', this.addActParams, true, res => {
        if (res.ret === '200000') {
          // 
          if (this.isPut) {
            // 
            this.$router.push(
              '/market/actTpl/actSetConf?form=' + res.data.form + '&tplCode=' + res.data.tplCode
            )
          } else {
            // 
            // 
            this.$router.push('/market/actTpl')
          }
          this.$message.success('!')
          return
        }
        this.$message.error(res.message)
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.el-tabs {
  padding: 0 20px;
  .el-tab-pane {
    padding: 30px;
    .ipone {
      float: left;
      width: 299px;
      margin: 0 20px;
      .header {
        position: relative;
        width: 100%;
        height: 66px;
        background: url('http://weiopn.oss-cn-beijing.aliyuncs.com/pc_data_front/img/937@2x.png')
          center no-repeat;
        p {
          position: absolute;
          bottom: 0;
          width: 100%;
          text-align: center;
          color: #fff;
          margin: 0;
        }
      }
      .content {
        position: relative;
        width: 100%;
        height: 483px;
        // background-color: red;
        .bg {
          width: 100%;
          height: 100%;
          transition: all 0.2s;
          img {
            width: 100%;
            height: 100%;
          }
        }
        .top {
          position: absolute;
          top: 118px;
          left: 50%;
          transform: translateX(-50%);
          width: 282px;
          height: 142px;
          img {
            width: 100%;
            height: 100%;
          }
        }
        .kits{
          position: absolute;
          top: 15px;
          left: 92%;
          transform: translateX(-50%);
          width: 40px;
          height: 40px;
          img {
            width: 100%;
            height: 100%;
          }
        }
        .game-con {
          position: absolute;
          left: 0;
          bottom: 42px;
          width: 100%;
          height: 144px;
          display: flex;
          justify-content: space-around;
          align-items: center;
          flex-wrap: wrap;
          .game-item {
            width: 25%;
            height: 50%;
            img {
              width: 100%;
              height: 100%;
            }
          }
        }
        .winning,
        .not-winning {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background: rgba(0, 0, 0, 0.5);
          .prize-con {
            width: 100%;
            height: 100%;
            color: #fff;
            text-align: center;
            .close {
              position: absolute;
              transform: translate(232px, 56px);
              width: 20px;
              height: 20px;
              line-height: 20px;
              text-align: center;
              border: 1px solid #fff;
              border-radius: 50%;
            }
            .pic {
              max-width: 196px;
              // max-height: 200px;
              margin-top: 84px;
              margin-right: 20px;
            }
            p {
              color: #fff;
            }
            .prize-btn {
              margin-top: 46px;
              max-width: 138px;
              max-height: 34px;
            }
          }
        }
        .not-winning {
          .prize-con {
            .pic {
              margin-right: 0;
            }
          }
        }
      }
      .footer {
        width: 100%;
        height: 64px;
        background: url('http://weiopn.oss-cn-beijing.aliyuncs.com/pc_data_front/img/phone-footer@2x.png')
          center no-repeat;
      }
    }
    .edit-box {
      float: left;
      width: 50%;
      margin: 0 20px;
      .edit-con {
        // 
        .img-con {
          position: relative;
          width: 100px;
          height: 100px;
          border: 1px dashed #d9d9d9;
          border-radius: 6px;
          margin: 0 10px;
          img {
            position: absolute;
            top:50%; 
            left:50%;
            transform: translate(-50%,-50%);
            max-width: 90%;
            max-height: 90%;
          }
        }
        .btn-con {
          position: relative;
          .el-button {
            position: absolute;
            bottom: 0;
          }
        }
        .edit-bg-img {
          display: flex;
          img {
            align-items: center;
            height: 100%;
          }
        }
        .edit-top-img {
          display: flex;
          // .img-con {
          //   width: 40%;
          //   display: flex;
          //   justify-content: center;
          //   align-items: center;
          //   img {
          //     width: 90%;
          //   }
          // }
        }
        .edit-game-img > div {
          display: flex;
          margin-bottom: 10px;
          // .img-con {
          //   display: flex;
          //   justify-content: center;
          //   align-items: center;
          //   img {
          //     width: 150px;
          //   }
          // }
        }
        .edit-winning-img {
          display: flex;
          img {
            width: 100%;
          }
        }
        .edit-notWinning-img {
          display: flex;
          // .img-con {
          //   display: flex;
          //   justify-content: center;
          //   align-items: center;
          //   img {
          //     width: 100%;
          //   }
          // }
        }
      }
      .tips {
        color: #999;
        margin-top: 10px;
        margin-bottom: 10px;
      }
    }
  }
}
// 
.el-tabs .el-tab-pane:first-child {
  .bg:hover,
  .game-item:hover,
  .top:hover {
    transform: scale(0.99);
    cursor: pointer;
    filter: brightness(60%);
    border: 2px dotted #fff;
    box-sizing: border-box;
  }
  .top:hover {
    transform: translateX(-50%) scale(0.99);
  }
}
.btn {
  text-align: center;
  p {
    font-size: 16px;
    margin-bottom: 20px;
  }
  .el-button {
    width: 130px;
    height: 40px;
  }
}
</style>




