<template>
  <!-- 
  Author: chenxin
  Create Date: 2018-10-18
  Description: 
  -->
  <div class="pond">
    <el-form ref="form" :model="awae" label-width="100px" :rules="rules">
      <el-form-item label="N:" v-if="nWin" prop="n">
         <el-input-number v-model="awae.n" :min="0" controls-position="right"></el-input-number> 
      </el-form-item>
      <el-form-item label=":" prop="type">
        <el-select v-model="awae.awardType" :disabled="awae.id ? true : false" placeholder="" @change="resetPrize">
          <el-option v-for="item in prizeList" :key="item.type" :label="item.name" :value="item.type">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label=":" prop="name">
        <el-col :span="10">
          <el-input v-model="awae.prizeName" :disabled="awae.id ? true : false" placeholder=""></el-input>
        </el-col>
      </el-form-item>
      <template v-if="awae.awardType == '1'">
        <el-form-item label=":" prop="pool">
          <!-- <el-button  @click="getEntityList"></el-button> -->
          <el-button v-if="!awae.awardPic" @click="getList"></el-button>
          <img style="border: 1px dotted #ccc" v-if="awae.awardPic" :src="awae.awardPic" @click="!awae.id && getList()">
          <span>{{awae.poolName}}</span>
        </el-form-item>
        <el-form-item label=":" prop="putNum">
          <el-input-number v-model="awae.totalNum" :disabled="awae.id ? true : false" :min="0" controls-position="right"></el-input-number> 
          <span v-if="awae.id ? true : false">
            <el-input-number v-model="residue" :disabled="true"></el-input-number>
            <el-button @click="addRepertory"></el-button>
          </span>
        </el-form-item>
      </template>
      <template v-if="awae.awardType == '2'">
        <el-form-item label=":" prop="pool">
          <!-- <el-button  @click="getVirtualList"></el-button> -->
          <el-button v-if="!awae.awardPic" @click="getList"></el-button>
          <img v-if="awae.awardPic" :src="awae.awardPic" @click="awae.id ? true : false && getList">
          <span>{{awae.poolName}}</span>
        </el-form-item>
        <el-form-item label=":" prop="putNum">
          <el-input-number v-model="awae.totalNum" :disabled="awae.id ? true : false" :min="0" controls-position="right"></el-input-number> 
          <span v-if="awae.id ? true : false">
            <el-input-number v-model="residue" :disabled="true"></el-input-number>
            <el-button @click="addRepertory"></el-button>
          </span>
        </el-form-item>
      </template>
      <template v-if="awae.awardType == '3'">
        <el-form-item label=":" prop="pool">
          <!-- <el-button  @click="getRedpacklList"></el-button> -->
          <el-button v-if="!awae.awardPic" @click="getList"></el-button>
          <img v-if="awae.awardPic" :src="awae.awardPic" @click="awae.id ? true : false && getList">
          <span>{{awae.poolName}}</span>
        </el-form-item>
        <!-- <el-col :span="10"> -->
        <el-form-item label=":" prop="redNum">
          <el-input-number v-model="awae.redMoney" :disabled="awae.id ? true : false" :precision="2" :min="0" controls-position="right" @change="countRedTotal"></el-input-number> 
        </el-form-item>
        <!-- </el-col>
        <el-col :span="14"> -->
        <el-form-item label=":" prop="putNum">
          <el-input-number v-model="awae.totalNum" :disabled="awae.id ? true : false" :min="0" controls-position="right" @change="countRedTotal"></el-input-number> 
          <span v-if="awae.id ? true : false">
            <el-input-number v-model="residue" :disabled="true"></el-input-number>
            <el-button @click="addRepertory"></el-button>
          </span>
        </el-form-item>
        <!-- </el-col> -->
        <el-form-item label=":" prop="redTotal">
          <el-input-number disabled v-model="totalRed" :min="0" controls-position="right"></el-input-number> 
        </el-form-item>
      </template>
      <template v-if="awae.awardType == '6'">
        <el-form-item label=":" prop="pool">
          <!-- <el-button  @click="getIntegrallList"></el-button> -->
          <el-button v-if="!awae.awardPic" @click="getList"></el-button>
          <img v-if="awae.awardPic" :src="awae.awardPic" @click="awae.id ? true : false && getList">
          <span>{{awae.poolName}}</span>
        </el-form-item>
        <el-form-item label=":" prop="putNum">
          <el-input-number v-model="awae.totalNum" :disabled="awae.id ? true : false" :min="0" controls-position="right"></el-input-number> 
          <span v-if="awae.id ? true : false">
            <el-input-number v-model="residue" :disabled="true"></el-input-number>
            <el-button @click="addRepertory"></el-button>
          </span>
        </el-form-item>
        <el-form-item label=":" prop="intTotal">
          <el-input-number v-model="awae.integral" :disabled="awae.id ? true : false" :min="0" controls-position="right"></el-input-number> 
        </el-form-item>
      </template>
      <el-form-item label=":" prop="probability">
        <el-input-number v-model="awae.probability" :min="0" :max="100" controls-position="right"></el-input-number> %
      </el-form-item>
      <el-form-item>
        <el-checkbox v-model="awae.hasWarn" @change="resetWarn"></el-checkbox>
        <span v-if="awae.hasWarn">
          <el-input-number v-model="awae.warnValue" :min="0" controls-position="right"></el-input-number> 
        </span>
      </el-form-item>
      <el-form-item v-if="awae.awardType !== '6'">
        <el-checkbox v-model="awae.giveScore" :checked="awae.giveScore == 1 ? true : false" :true-label=1 :false-label=0 @change="resetScore"></el-checkbox>
        <span v-if="awae.giveScore">
          <el-button class="ml20 mr20" v-if="!awae.integralPool" @click="giveIntegral"></el-button>
          <el-button size="mini" type="info" v-else @click="giveIntegral"></el-button>
          <el-input-number v-model="awae.integral" :min="0" controls-position="right"></el-input-number> 
        </span>
      </el-form-item>
      <el-form-item>
        <el-checkbox v-model="awae.guideGzh" :checked="awae.guideGzh == 1 ? true : false" :true-label=1 :false-label=0></el-checkbox>
        <el-checkbox v-if="!isRed" v-model="awae.hasPdMaxOut" @change="resetPdMaxOut"></el-checkbox>
        <span v-if="awae.hasPdMaxOut">
          <el-input-number v-model="awae.pdMaxOut" :min="0" controls-position="right"></el-input-number> 
        </span>
      </el-form-item>
    </el-form>

    <!--  -->
    <el-dialog :title="title" :visible.sync="listVisible" width="800px">
      <el-table :data="list" border :stripe="true" style="width: 100%">
        <el-table-column prop="name" label="" align="center">
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <img :src="scope.row.pic" alt="" style="height: 60px">
          </template>
        </el-table-column>
        <el-table-column prop="stock" label="" align="center">
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-button size="mini" @click="selectPrize(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-col :span="24">
        <el-pagination background @size-change="handSizeChange" @current-change="handCurrentChange" :current-page="params.pageNo" :page-size="params.pageSize" layout="total, prev, pager, next, jumper" :total="listTotal">
        </el-pagination>
      </el-col>
      <div style="clear:both"></div>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="integralVisible" width="800px">
      <el-table :data="integralList" border :stripe="true" style="width: 100%">
        <el-table-column prop="name" label="" align="center">
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <img :src="scope.row.pic" alt="" style="height: 60px">
          </template>
        </el-table-column>
        <el-table-column prop="stock" label="" align="center">
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-button size="mini" v-if="awae.integralPool !== scope.row.id" @click="selectIntegral(scope.row)"></el-button>
            <el-button size="mini" type="info" v-else @click="selectIntegral(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-col :span="24">
        <el-pagination background @size-change="handSizeChange" @current-change="handCurrentChange" :current-page="params.pageNo" :page-size="params.pageSize" layout="total, prev, pager, next, jumper" :total="integralTotal">
        </el-pagination>
      </el-col>
      <div style="clear:both"></div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  props: ['awae', 'prizeType', 'nWin', 'isRed'],
  data() {
    var validateImgUrl = (rule, value, callback) => {
      if (this.awae.awardPic) {
        callback()
      } else {
        callback(new Error(''))
      }
    }
    var tips = (rule, value, callback) => {
      // callback()
    }
    return {
      // awae: this.awae,
      prizeList: this.prizeType,
      rules: {
        n: [{required: true, validator: tips}],
        type: [{required: true, validator: tips}],
        name: [{required: true, validator: tips}],
        probability: [{required: true, validator: tips}],
        redNum: [{required: true, validator: tips}],
        pool: [{required: true, validator: tips}],
        putNum: [{required: true, validator: tips}],
        intTotal: [{required: true, validator: tips}],
        redTotal: [{required: true, validator: tips}],
        // pool: [{required: true, validator: tips}],
      },
      title: '',
      list: [],
      params: {
        metraFlag: '',
        pageNo: 1,
        pageSize: 10,
        status: 1
      },
      listTotal: 0,
      listVisible: false,

      integralList: [],
      integralTotal: 0,
      integralVisible: false,
    }
  },
  computed: {
    residue: {
      get: function() {
        return this.awae.totalNum - this.awae.outNum
      },
      set: function(newValue) {
        // console.log(newValue)
      }
    },
    totalRed: {
      get: function() {
        return this.awae.redMoney * this.awae.totalNum
      },
      set: function(newValue) {
        // console.log(newValue)
      }
    }
  },
  created() {
    // if (this.awae.id) {
    //   this.isEdit = true
    // } else {
    //   this.isEdit = false
    // }
    // console.log(this.awae)
  },
  methods: {
    // 
    selectPrize(obj) {
      this.awae.awardPic = obj.pic
      this.awae.poolName = obj.name
      this.awae.poolId = obj.id
      this.listVisible = false
    },
    // 
    resetPrize() {
      this.awae.awardPic = ''
      this.awae.prizeName = ''
      this.awae.poolName = ''
      this.awae.totalNum = ''
      this.awae.probability = ''
      this.awae.redMoney = ''
      this.awae.redTotalMoney = ''
      this.awae.integral = ''
    },
    getList() {
      if (this.awae.awardType == '1') {
        this.params.metraFlag = 'object'
        this.title = ''
      } else if (this.awae.awardType == '2') {
        this.params.metraFlag = 'virtual'
        this.title = ''
      } else if (this.awae.awardType == '3') {
        this.params.metraFlag = 'redpack'
        this.title = ''
      } else if (this.awae.awardType == '6') {
        this.params.metraFlag = 'integral'
        this.title = ''
      }
      this.$request.post('/api/saotx/metra/list', this.params, true, res => {
        if (res.ret === '200000') {
          this.list = []
          this.list = res.data.list
          this.listTotal = res.data.page.count
          this.listVisible = true
          return
        }
        this.$message.error(res.message)
      })
    },
    // 
    giveIntegral() {
      this.$request.post(
        '/api/saotx/metra/list',
        {
          metraFlag: 'integral',
          pageNo: 1,
          pageSize: 10,
          status: 1
        },
        true,
        res => {
          if (res.ret === '200000') {
            this.list = []
            this.integralList = res.data.list
            this.integralTotal = res.data.page.count
            this.integralVisible = true
            return
          }
          this.$message.error(res.message)
        }
      )
    },
    // 
    selectIntegral(obj) {
      this.awae.integralPool = obj.id
      this.integralVisible = false
    },
    handSizeChange(newSize) {
      this.params.pageSize = newSize
      this.getList()
    },
    handCurrentChange(newSize) {
      this.params.pageNo = newSize
      this.getList()
    },
    // 
    resetScore(val) {
      if (!val) {
        this.awae.integralPool = null
        this.awae.integral = ''
        this.awae.giveScore = 0
      } else {
        this.awae.giveScore = 1
      }
    },
    // 
    resetWarn(val) {
      if (!val) {
        this.awae.hasWarn = null
        this.awae.warnValue = ''
      }
    },
    resetPdMaxOut(val) {
      if (!val) {
        this.awae.pdMaxOut = ''
      }
    },
    countRedTotal(val) {
      const redMoney = this.awae.redMoney
      const totalNum = this.awae.totalNum
      if (redMoney != 0 && totalNum != 0) return this.awae.redTotalMoney = redMoney * totalNum
    },
    // 
    addRepertory() {
      this.$prompt('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          inputPattern: /^\d{1,}$/,
          inputErrorMessage: ''
        }).then(({ value }) => {
          if (value == 0) return this.$message.error('0')
          this.$request.post('/api/saotx/act/addNum', {
            id: this.awae.id,
            increment: value
          }, true, res => {
            if (res.ret === '200000') {
              this.$message.success('')
              this.awae.totalNum += Number(value)
            } else {
              this.$message.error(res.message)
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });       
        });
    }
  }
}
</script>
<style lang="scss" scoped>
.el-pagination {
  margin-top: 20px;
}
// .el-form-item {
//   margin-bottom: 10px;
// }
img {
  width: 120px;
  height: 90px;
}
</style>



