<template>
  <!-- 
  Author: chenxin
  Create Date: 2018-10-18
  Description: 
  -->
  <div class="actSetConf-container">
    <el-breadcrumb separator-class="el-icon-arrow-right"v-show='form!="act-501"'>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>   
    <el-card>
    	<el-steps :active="stepActive" finish-status="success" align-center class='step-style' v-show='form=="act-501"'>
			  <el-step title=""></el-step>
			  <el-step title=""></el-step>
			  <el-step title=""></el-step>
			</el-steps>
      <el-form ref="actSetConfRef" :model="confData" label-width="150px" :rules="confRules">
        <el-form-item label="" prop="actName">
          <el-input v-model="confData.actName" maxLength='15' placeholder='15'></el-input>
        </el-form-item>
        <el-form-item label="" prop="note">
          <el-input type="textarea" v-model="confData.note" :rows="3" resize="none" maxLength='15' placeholder='15'></el-input>
        </el-form-item>
        <el-form-item label="" prop="idx" v-if="form != 'act-301'">
          <el-select v-model="confData.idx" placeholder="">
            <el-option v-for="(val, key, index) in idxSelect" :key="index" :label="val" :value="key">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="date">
          <el-date-picker v-model="actTime" :time-arrow-control="true" format="yyyy-MM-dd HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss" type="datetimerange" :editable="false" range-separator="" start-placeholder="" end-placeholder="">
          </el-date-picker>
        </el-form-item>
        <!--  -->
        <el-form-item label="" prop="banner" v-if="form !== 'act-301'">
          <el-upload class="avatar-uploader" :before-upload="beforeAvatarUpload" :action="uploadURL" :headers="headerObj" :on-success="upBannerImg" :show-file-list="false">
            <img v-if="confData.banner" :src="confData.banner" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            <div slot="tip" class="el-upload__tip">750*270pngjpg2M</div>
          </el-upload>
        </el-form-item>
        <!--  -->
        <el-form-item label="" prop="banner" v-else>
          <el-upload class="avatar-uploader" :before-upload="beforeAvatarUpload" :action="uploadURL" :headers="headerObj" :on-success="upBannerImg" :show-file-list="false">
            <img v-if="confData.banner" :src="confData.banner" class="avatar red-packet">
            <i v-else class="el-icon-plus avatar-uploader-icon red-packet"></i>
            <div slot="tip" class="el-upload__tip">200*200pngjpg2M</div>
          </el-upload>
        </el-form-item>
        <el-form-item v-if="form == 'act-301'" label="" prop="number">
          
          <el-input-number v-model="redConf.joinNum" :precision="0" :min="1" controls-position="right"></el-input-number>
          
        </el-form-item>
        <el-form-item v-if="form == 'act-301'" label="" prop="isShare">
          <el-radio v-model="redConf.share" :label="1"></el-radio>
          <el-radio v-model="redConf.share" :label="0"></el-radio>
        </el-form-item>
        <el-form-item label="" prop="desc">
          <quill-editor ref="myTextEditor" v-model="confData.actDesc" :options="editorOption" placeholder="300" @blur="onEditorBlur($event)">
          </quill-editor>
        </el-form-item>
        <el-form-item label="" prop="quesTime" v-show='form=="act-501"'>
        	<el-radio v-model="extInfo.limited" :label="1"></el-radio>
  				<el-radio v-model="extInfo.limited" :label="2"><input v-model="extInfo.time" type='number' class='limited-time' @input='limitNum'/></el-radio>
        </el-form-item>
        <el-form-item label="">
          <el-radio v-model="confData.showStatus" :label="1"></el-radio>
          <el-radio v-model="confData.showStatus" :label="0"></el-radio>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="nextStep"></el-button>
          <el-button @click="$router.push('/market/actTpl')"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>
<script>
import { quillEditor } from 'vue-quill-editor'
export default {
  props: ['form', 'tplCode', 'id', 'clone'],
  components: {
    quillEditor
  },
  data() {
    var validateDate = (rule, value, callback) => {
      if (this.actTime && this.actTime.length === 2) {
        callback()
      } else {
        callback(new Error(''))
      }
    }
    var validateIdx = (rule, value, callback) => {
      if (value == '0') {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    var validateBanner = (rule, value, callback) => {
      if (!this.confData.banner) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    var validateDesc = (rule, value, callback) => {
      if (!this.confData.actDesc) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    var valideShare = (rules, value, callback) => {
      callback()
    } 
    var valideNumber = (rules, value, callback) => {
      if (this.redConf.joinNum) {
        callback()
      } else {
        callback(new Error(''))
      }
    } 
    return {
      // 
      editorOption: {
        modules: {
          toolbar: [
            [{ header: 1 }, { header: 2 }],
            [{ list: 'ordered' }, { list: 'bullet' }],
            ['bold', 'italic', 'underline', 'strike', , 'blockquote'],
            [{ color: [] }, { background: [] }, { align: [] }]
          ]
        },
        placeholder: ''
      },
      extInfo:{
      	limited:1,
      	time:60
      },
      idxSelect: {},
      confData: {
        actDesc: '',
        actName: '',
        banner: '',
        form: '',
        idx: '1',
        note: '',
        stimeStr: '',
        etimeStr: '',
        showStatus: 1,
        tplCode: '',
        extInfo: '',
        number: ''
      },
      redConf: {
        joinNum: 1,
        share: 0
      },
      confRules: {
        actName: [{ required: true, message: '', trigger: 'blur' }],
        note: [{ required: true, message: '', trigger: 'blur' }],
        date: [{ required: true, validator: validateDate, trigger: 'change' }],
        idx: [{ required: true, validator: validateIdx, trigger: 'change' }],
        banner: [{ required: true, validator: validateBanner }],
        desc: [{ required: true, validator: validateDesc }],
        number: [{ required: true, validator: valideNumber }],
        isShare: [{ required: true, validator: valideShare }]
      },
      actTime: [],
      uploadURL: '/api/saotx/attach/commonAliUpload',
      headerObj: {
        loginId: sessionStorage.getItem('access_loginId') || '2d07e7953a2a63ceda6df5144d1abec3',
        token: sessionStorage.getItem('access_token'),
        CLIENTSESSIONID: sessionStorage.getItem('CLIENTSESSIONID')
      },
      stepActive:0,
    }
  },
  watch: {
    actTime: function(val) {
      if (val && val.length === 2) {
        this.confData.stimeStr = this.actTime[0]
        this.confData.etimeStr = this.actTime[1]
      } else {
        this.confData.stimeStr = ''
        this.confData.etimeStr = ''
      }
    }
  },
  created() {
    this.getDetail()
    this.getIdxSelect()
  },
  methods: {
  	limitNum(){
  		var len=4;
  		if(this.extInfo.time.length>len){
  			var str=this.extInfo.time.slice(0,len);
  			this.extInfo.time=parseInt(str);
  		}
  		
  	},
    getDetail() {
      if (!this.id) return
      this.$request.post('/api/saotx/act/detail', { id: this.id }, true, res => {
        if (res.ret == '200000') {
          if (this.clone == '1') {
            for (let key in this.confData) {
              this.confData[key] = res.data.act[key]
            }
          } else {
            this.confData = res.data.act
          }
          if (this.form == 'act-301') this.redConf = JSON.parse(this.confData.extInfo)
          this.confData.idx = this.confData.idx + ''
          if (this.redConf.extInfo) this.extInfo=JSON.parse(this.confData.extInfo)
          this.actTime.push(this.confData.stimeStr)
          this.actTime.push(this.confData.etimeStr)
          return
        }
        this.$message.error(res.messgae)
      })
    },
    // 
    getIdxSelect() {
      this.$request.post('/api/saotx/act/idxSelect', {}, true, res => {
        if (res.ret === '200000') return this.idxSelect = res.data
      })
    },
    // 
    upBannerImg(resule) {
      if (resule.ret === '200000') {
        this.confData.banner = resule.data.accessUrl
        this.$refs.actSetConfRef.validateField('banner', valid => {})
        return
      }
      this.$message.error(resule.message)
    },
    // 
    onEditorBlur() {
      this.$refs.actSetConfRef.validateField('desc', valid => {})
    },
    nextStep() {
      this.$refs.actSetConfRef.validate(valid => {
        if (!valid) return this.$message.error('!')
        if(this.form=='act-501'){
        	if(this.extInfo.limited==1){
	        	if(this.extInfo.time<=0 || !this.extInfo.time){
	        		 return this.$message.error('!')
	        	}
	        }
        }       
        if (!this.id) {
          this.confData.form = this.form;
          if(this.form=='act-501'){
          	this.confData.extInfo=JSON.stringify(this.extInfo);
          }         
          this.confData.tplCode = this.tplCode
        }else {
        	if(this.form=='act-501'){
          	this.confData.extInfo=JSON.stringify(this.extInfo);
          } 
        }
        if (this.form == 'act-301') this.confData.extInfo = JSON.stringify(this.redConf)
        this.$request.post('/api/saotx/act/saveOrModify', this.confData, true, res => {
          if (res.ret === '200000') {
          	if(this.form=='act-501'){
          		return this.$router.push(
              '/market/actTpl/quesActSetConf?id=' + res.data.id + '&actCode=' + res.data.actCode+'&form='+this.form
            	)
          	} else if (this.form=='act-301') {
              return this.$router.push(`/market/actTpl/redPut?id=${res.data.id}&actCode=${res.data.actCode}`)
            } else {
          		return this.$router.push(
              '/market/actTpl/actPutConf?id=' + res.data.id + '&actCode=' + res.data.actCode
            	)
          	}
            
          }
          this.$message.error(res.message)
        })
      })
    },
    beforeAvatarUpload(file) {
      const JPGOrPNG = file.type === 'image/jpeg' || file.type === 'image/png'
      const isLt2M = file.size / 1024 / 1024 < 2
      if (!JPGOrPNG) {
        this.$message.error(' JPG  PNG !')
      }
      if (!isLt2M) {
        this.$message.error(' 2MB!')
      }
      return JPGOrPNG && isLt2M;
    }
  }
}
</script>
<style lang="scss" scoped>
.el-input,
.el-textarea {
  width: 300px;
}
.step-style {
	margin-bottom: 40px;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 300px;
  height: 108px;
  line-height: 108px;
  text-align: center;
  &.red-packet {
    width: 100px;
    height: 100px;
  }
}
.avatar {
  width: 300px;
  height: 108px;
  display: block;
  &.red-packet {
     width: 100px;
    height: 100px;
  }
}
.quill-editor {
  width: 420px;
}
.limited-time {
	width:60px;
	margin-left: 10px;
	margin-right: 10px;
	height: 24px;
	border-radius: 4px;
	border:none;
	border:1px solid #ccc;
}
</style>



